<template>
  <div class="create">
    <br>
    <br>
    <form @submit="create">
      <input
        type="text"
        v-model="fullName"
        name="n"
        placeholder="Full name..."
        class="cred"
        id="flName"
      >
      <br>
      <input type="text" v-model="email" name="e" placeholder="Email..." class="cred" id="email">
      <br>
      <input
        type="password"
        v-model="pass"
        name="p"
        placeholder="Password..."
        class="cred"
        id="pass"
      >
      <br>
      <textarea v-model="bio" name="b" cols="30" rows="5" class="cred" id="bio"></textarea>
      <br>
      <br>
      <input type="submit" value="Create Account" class="btn">
    </form>
  </div>
</template>

<script>
import { MYDB } from "../setupMyFirebase.js";

export default {
  name: "create",
  data: function() {
    return {
      email: "",
      password: ""
    };
  },

  methods: {
    create: function(e) {
      MYDB.auth()
        .createUserWithEmailAndPassword(this.email, this.pass)
        .then(
          user => {
            alert(`Account created for $(user.email}`);
            this.$router.push("/");
          },
          err => {
            alert(err.message);
          }
        );
      e.preventDefault();
    },
    create() {},
    login() {},
    logout() {}
  },
  created: {}
};
</script>

<style scoped>
.btn {
  background: #0065a4;
  color: white;
  margin-left: auto;
  margin-right: auto;
  display: block;
  font-weight: bold;
  font-size: 16px;
}
.btn:hover {
  font-size: 18px;
}
.cred {
  padding-right: 10px;
  font-size: 20px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.info {
  text-align: center;
  padding-bottom: 10px;
  font-style: italic;
  font-size: 20px;
  font-weight: bold;
}
.hide {
  display: none;
}
#bio {
  height: 100px;
  font-size: 14px;
}
</style>